import httpClient from './http-client'

async function achieveCoupon(mobile, brandId) {
  let resp = await httpClient.post('/coupons/achieve', {mobile: mobile, brand_id: brandId})
  return resp.data
}

async function getCoupons(mobile) {
  let resp = await httpClient.get('/coupons', {params: {mobile: mobile}})
  return resp.data
}

export default {
  achieveCoupon,
  getCoupons
}