<template>
  <transition :name="transition">
    <div tabindex="0"
         class="vld-overlay is-active"
         :class="{ 'is-full-page': isFullPage }"
         v-show="isActive"
         :aria-busy="isActive"
         aria-label="Loading"
         :style="{ zIndex }">
      <div class="vld-background"
           @click.prevent="cancel"
           :style="{ background: backgroundColor,    }">
      </div>
      <div class="vld-icon">
        <slot name="before"/>
        <slot name="default">
          <component :is="loader" :color="color" :width="width" :height="height"/>
        </slot>
        <slot name="after"/>
      </div>
    </div>
  </transition>
</template>

<script>
  import {removeElement, HTMLElement} from './helpers.js'
  // import trapFocusMixin from './trapFocusMixin.js';
  import Loaders from './loaders/index.js';
  export default {
    name: 'vue-loading',
    mixins: [],
    props: {
      active: Boolean,
      programmatic: Boolean,
      container: [Object, Function, HTMLElement],
      isFullPage: {
        type: Boolean,
        default: true
      },
      transition: {
        type: String,
        default: 'fade'
      },
      color: String,
      backgroundColor: String,
      opacity: Number,
      width: Number,
      height: Number,
      zIndex: Number,
      loader: {
        type: String,
        default: 'spinner'
      }
    },
    data() {
      return {
        // Don't mutate the prop
        isActive: this.active
      }
    },
    components: Loaders,
    beforeMount() {
      // Insert the component in DOM when called programmatically
      if (this.programmatic) {
        if (this.container) {
          this.isFullPage = false;
          this.container.appendChild(this.$el)
        } else {
          document.body.appendChild(this.$el)
        }
      }
    },
    mounted() {
      // Activate immediately when called programmatically
      if (this.programmatic) {
        this.isActive = true;
      }
    },
    methods: {
      hide() {
        this.$emit('hide');
        this.$emit('update:active', false);
        // Timeout for the animation complete before destroying
        if (this.programmatic) {
          this.isActive = false;
          setTimeout(() => {
            this.$destroy();
            removeElement(this.$el)
          }, 1500)
        }
      }
    },
    watch: {
      active(value) {
        this.isActive = value
      }
    }
  }
</script>

<style scoped>
.vld-overlay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  align-items: center;
  display: none;
  justify-content: center;
  overflow: hidden;
  z-index: 9999;
}

.vld-overlay.is-active {
  display: flex;
}

.vld-overlay.is-full-page {
  z-index: 9999;
  position: fixed;
}

.vld-overlay .vld-background {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  background: #000;
  opacity: 0.2;
}

.vld-overlay .vld-icon, .vld-parent {
  position: relative;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .6s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>