import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import VueClipboard from 'vue-clipboard2'

import Api from './api'
import LoadingIndicator from './components/loading-indicator'
import NavBar from './components/NavBar'

import Pages from './pages'
import Stores from './store'

import Agent from './helpers/agent.js'

import './assets/styles/app.css'

Vue.use(VueRouter)
Vue.use(Vuex)

// 领券页面复制到剪切板
Vue.use(VueClipboard)

Vue.use(LoadingIndicator)

Vue.config.productionTip = false

Vue.component("nav-bar", NavBar)

Vue.prototype.$api = Api
// Vue.prototype.$loading = LoadingIndicator
Vue.prototype.$agent = Agent

let router = new VueRouter({
  routes: Pages.routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
    // return savedPosition
  }
})

let DEFAULT_TITLE = "2020 AW 云上发布会"

var pageHistory = []
Vue.prototype.$pageHistory = pageHistory
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || DEFAULT_TITLE

  if (!Agent.isInApp()) {
    if (to.path == pageHistory[pageHistory.length - 2] && from.path == pageHistory[pageHistory.length - 1]) {
      pageHistory.pop()
    } else {
      pageHistory.push(to.path)
    }
  }

  next()
})

let store = new Vuex.Store(Stores)

new Vue({
  router,
  store
}).$mount('#app')
