import $api from '../api'

export default {
  state: () => ({
    coupon: {}
  }),
  mutations: {},

  actions: {
    async achieveCoupon(ctx, payload) {
      let data = await $api.achieveCoupon(payload.mobile, payload.brandId)
      if (data.coupon) {
        ctx.state.coupon = data.coupon
      } else {
        console.log(data.status, data.message)
      }
    },

    async getCoupons(ctx, payload) {
      let data = await $api.getCoupons(payload.mobile)
      if (data.coupons) {
        ctx.state.coupon = data.coupons[0]
      } else {
        console.log(data.status, data.message)
      }
    }
  },

  getters: {}
}