<template>
  <div v-bind:class="{ 'has-nav-bar': !this.$agent.isInApp() }">
    <nav-bar></nav-bar>
    <div class='brand-list'>
      <div v-for="brand in brands" :key="brand.id">
        <router-link :to="{ name: 'vr', params: {id: brand.id} }">
          {{ brand.name}}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex'

  export default {
    name: 'BrandList',

    computed: {
      ...mapState({
        brands: state => state.brand.brands
      })
    },

    methods: {
      ...mapActions(['getBrands']),
    },

    mounted() {
      this.getBrands()
    }

  }
</script>

