import $api from '../api'

export default {
  // namespaced: true,

  state: () => ({
    brands: [],
    brand: {}
  }),

  mutations: {
  },

  actions: {
    async getBrands(ctx) {
      let data = await $api.getBrands()
      ctx.state.brands = data.brands
    },

    async getBrand(ctx, id) {
      let data = await $api.getBrand(id)
      ctx.state.brand = data.brand
    }
  },

  getters: {
  }

}