<template>
  <div>
    <h1>Coupons</h1>

    <div class='mobile-field'>
      请输入手机号
      <input v-model="mobile" />
    </div>

    <div class='brand-list'>
      <span v-for="brand in brands" :key="brand.id"
        class='brand' v-bind:class="{ selected: selectedBrandId == brand.id }"
        v-on:click="selectBrand(brand)" >
        {{ brand.name }}
      </span>
    </div>

  <div>
    {{coupon.code}}
  </div>

  <button v-on:click="genCoupon">生成优惠码</button>

  <button v-on:click="showCoupon">查看我的优惠券</button>

  <button type="button"
    v-clipboard:copy="coupon.code"
    v-clipboard:success="onCopy">
    Copy!
  </button>

  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex'

  export default {
    name: 'Coupons',

    data() {
      return {
        mobile: "",
        selectedBrandId: null
      }
    },

    computed: {
      ...mapState({
        brands: state => state.brand.brands,
        coupon: state => state.coupon.coupon
      })
    },

    methods: {
      ...mapActions(['getBrands', 'achieveCoupon', 'getCoupons']),

      genCoupon() {
        this.achieveCoupon({
          mobile: this.mobile,
          brandId: this.selectedBrandId
        })
      },

      selectBrand(brand) {
        this.selectedBrandId = brand.id
      },

      showCoupon() {
        this.getCoupons({mobile: this.mobile})
      },

      onCopy() {
        console.log('copy success')
      }
    },

    mounted() {
      this.getBrands()
    }
  }
</script>

<style>

  .brand {
    margin: 10px;
  }

  .brand.selected {
    color: orange;
  }

</style>
