import Component from './Loading'

const Api = (Vue, gloablProps = {}, globalSlots = {}) => {
  return {
    show(props = gloablProps, slots = globalSlots) {
      console.log("Loading show")
      const forceProps = {
        programmatic: true
      }
      const propsData = Object.assign({}, gloablProps, props, forceProps)

      const instance = new (Vue.extend(Component)) ({
        el: document.createElement('div'),
        propsData
      })

      const mergedSlots = Object.assign({}, globalSlots, slots)

      Object.keys(mergedSlots).map( name => {
        instance.$slots[name] = mergedSlots[name]
      })

      return instance
    }
  }
}

export default Api