<template>
  <div v-bind:class="{ 'has-nav-bar': !this.$agent.isInApp() }">
    <nav-bar>{{ brand.name }}</nav-bar>

    <video-container href="http://192.168.3.152:8080/behinder.mp4" type="video/mp4"
      poster="http://192.168.3.152:8080/1.jpg">
    </video-container>

    <img v-for="img in [4,5,6,7,8,9]" :key="img"
      v-bind:src="'http://192.168.3.152:8080/' + img + '.jpg'"
      style="width:100%"
      />

    <div v-bind:style="{ height: 300 + 'px' }" style="width:100%">
      <vr-viewer href="http://192.168.3.152:3000/5c599f6edf8f4c79bed40aea4b601fd3.jpg"
         showFullScreenBtn
         @fullScreen="gotoVr" >
      </vr-viewer>
    </div>

    <div><router-link to='lookbook'>Look Book</router-link></div>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex'
  import VrViewer from '../components/VrViewer'
  import VideoContainer from '../components/VideoContainer'

  export default {
    name: 'ShowRoom',
    components: {
      VrViewer,
      VideoContainer
    },
    computed: {
      ...mapState({
        brand: state => state.brand.brand
      })
    },
    mounted() {
      this.refreshBrand()
    },
    methods: {
      ...mapActions(['getBrand']),
      refreshBrand() {
        this.getBrand(this.$route.params.id).then( () => {
          document.title = this.brand.name
        })
      },
      gotoVr() {
        this.$router.push({ name: 'vr', params: { id: this.brand.id } })
      }
    },

    watch: {
      $route() {
        this.refreshBrand()
      }
    }

  }
</script>

